*,*::before,*::after{
    box-sizing: border-box;
}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}
body{
    font-family: 'Roboto','Noto Sans TC', sans-serif;
    line-height: $light-hight-bace;
}
a{
    text-decoration: none;
    color: $baceColor-bark;
    display: block;
}
.bg-style{
    background: $secondary;
}
.btn-style{
    background: $primary;
    color: $baceColor-light;
    &:hover{
      background: $font-color-index;
    }
}
.color-primary{
  color: $primary;
}
.color-pale{
  color: $baceColor-pale;  
}
.color-index{
  color: $font-color-index;
}
.color-page-pale{
  color: $font-color-page;
  &:hover{
    color: $baceColor-light;
    background: $baceColor-bark;
  }
}
.bg-page{
  background: $bg-color-page;
  &:hover{
    background: $baceColor-bark;
  }
}
.bg-ui-dark{
  background: $produce-ui-dark;
}
.bg-ui-colorful{
  background: $produce-ui-colorful;
}

.border-pale{
  border: 1px solid $border-pale;
}
.border-page-hover{
  border-bottom: $spacing-bace solid $baceColor-light;
  &:hover{
    border-bottom: $spacing-bace solid $primary;
}
}
.border-tb-deep{
  border-top: 1px solid $font-color-page;
  border-bottom: 1px solid $font-color-page;
}
.border-r-deep{
  border-right: 1px solid $font-color-page;
}
.border-l-deep{
  border-left: 1px solid $font-color-page;
}


@mixin pad{
  @media(max-width: 992px){
    @content;
  }
}
@mixin mobile{
  @media(max-width: 767px){
	  @content;
  }
}
.container{
  max-width: 1320px;//要加入padding
  margin: 0 auto;
  padding: 0 $spacing-bace*1.5;//卡片不貼齊容器，沒加會出現x軸
  @include pad{
    max-width: 720px;
  }
  @include mobile{
    max-width: 372px;
  }
}

.font-size-01{
  font-size: $font-size-bace*0.875;//14px
  line-height: $light-hight-bace*0.875;//21px
  @include mobile{
    font-size: $font-size-bace;
    line-height: $light-hight-bace;
  }
}
.font-size-02{
  font-size: $font-size-bace;//16px
  line-height: $light-hight-bace;//24px
}
.font-size-03{
  font-size: $font-size-bace*1.25;//20px
  line-height: $light-hight-bace*1.25;//30px
}
.font-size-04{
  font-size: $font-size-bace*1.5;//24px
  line-height: $light-hight-bace*1.5;//36px
}
.font-size-05{
  font-size: $font-size-bace*2.25;//36px
  line-height: $light-hight-bace*2.25;//54px
  @include mobile{
    font-size: $font-size-bace;
    line-height: $light-hight-bace;
  }
}
.font-size-06{
  font-size: $font-size-bace*4;//64px
  line-height: $light-hight-bace*4;//96px
  @include mobile{
    font-size: $font-size-bace*1.5;//24px
    line-height: $light-hight-bace*1.5;//36px
  }
}
.font-size-07{
  font-size: $font-size-bace*2;//32px
  line-height: $light-hight-bace*2;//48px
  @include pad{
    font-size: $font-size-bace*1.5;//24px
    line-height: $light-hight-bace*1.5;//36px
  }
}
.font-size-series{
  font-size: $font-size-bace*2.625;//42px
  line-height: $light-hight-bace*2.625;//63px
  span{
    font-size: $font-size-bace*2;//32px
    line-height: $light-hight-bace*2;//48px
    @include mobile{
      font-size: $font-size-bace*1.5;//24px
      line-height: $light-hight-bace*1.5;//36px
    }
  }
  @include mobile{
    font-size: $font-size-bace*1.5;//24px
    line-height: $light-hight-bace*1.5;//36px
  }
}
.font-size-page-l{
  font-size: $font-size-bace*1.1875;//19px
  line-height: $light-hight-bace*1.2083;//29px
  @include mobile{
    font-size: $font-size-bace;//16px
    line-height: $light-hight-bace*1.0833;//26px
  }
}
.font-size-page-s{
  font-size: $font-size-bace*1.125;//18px
  line-height: $light-hight-bace*1.125;//27px
  @include mobile{
    font-size: $font-size-bace;//16px
    line-height: $light-hight-bace;//24px
  }
}

.font-weight-01{
  font-weight: $font-weight-bace*0.75;//300
}
.font-weight-03{
  font-weight: $font-weight-bace*1.25;//500
}
.font-weight-04{
  font-weight: $font-weight-bace*1.75;//700
}
.font-weight-05{
  font-weight: $font-weight-bace*2.25;//900
}

